.Footer{
	background-color: #01191d;
	color: #fff;
    overflow: hidden;
    padding: 0px 10px;
}
.footer-header{
	position: relative;
    padding-top: 30px;
}
.footer-header::after{
	content: '';
    display: block;
    width: 100px;
    height: 3px;
    margin: 50px auto 10px auto;
    background-color: #1787fb;
    -webkit-box-shadow: 0 4px 20px #1787fb;
    box-shadow: 0 4px 20px #1787fb;
}
.footer-bottom{
	background-color: #2f2f2f;
	padding-bottom: 7px;
}
.footer-content>p{
    font-size: 12px;
    margin-top: 50px;
    margin-bottom: 0;
    padding-bottom: 5px;
}

.footer-misc{
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    position: relative;
}
.footer-logo > img{
    height: 250px;
}
.footer-text{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    height: 200px;
    z-index: 1;
}
.footer-text div{
    width: 100%;
}
.footer-text a {
    color: #e60040;
    text-decoration: underline;
}
.footer-text span{
    color: #f9a7a6;
}

@media(max-width: 768px){
    .footer-logo{
        position: absolute;
        z-index: 0;
        filter: opacity(20%);
    }
}