.navbar{
	background-color: #01191d;
	width: 100%;
  	display: flex;
  	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}

.navbar__row{
	height: 100%;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;
}
.navbar__logo{
	width: 40%;
}
.navbar-logo{
	aspect-ratio: 1/1;
	max-height: 150px;
}
.navbar__logo>a{
	color: white;
	font-size: 26px;
}

.navbar__links{
	margin: 0;
	padding: 0;
	width: 60%;
	height: 150px;
	list-style: none;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
}
.navbar__links>li{
	width: 100%;
	box-sizing: border-box;
	border-right: 1px solid #434957;
	height: 100%;
	border-radius: 0px;
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.navbar__links>li>a:hover{
	text-shadow: 0px 0px 5px #e4f1fe;
}
.navbar__links>li>a>h2{
	margin: 0px;
}
.navbar__links>li>.navbar__link{
	width: 50%;
	justify-content: center;
}


.navbar__link{
	text-decoration: none;
	color: #ffffff;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}
.navbar__info{
	color: #fff;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	height: 100%;
	word-break: break-word;
	text-shadow: none;
}
.navbar__info:hover{
	text-shadow: none;
}
.navbar__info>h5{
	padding: 0;
	margin: 0;
}
.tech-support{
	color: #1787fb;
	text-decoration: underline;
}

@media(max-width: 768px){
	.navbar__logo{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60%;
	}

	.navbar-logo{
	max-height: 100px;
	}

	.navbar__row .navbar__links{
		width: 100%;
	}
	.navbar__links{
		height: 100px;
	}
}

@media only screen and (max-width: 479px){
	.navbar__links>li{
		width: 50%;
		justify-content: center;
		border: none;
	}

	.navbar__logo{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
	}
	.navbar__row{
		flex-direction: row;
	}

	.navbar__links{
		width: 40%;
		height: 100px;
	}
	.navbar__links{
		flex-direction: column;
		align-items: center;
		justify-content: space-around;
	}

}

